.about_banner {
    padding-top: 140px;
    padding-bottom: 140px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    .banner_content {
        background-color: #fff;
        box-shadow: 10px 10px 0px #0c1420;
        padding: 50px 30px;
        border-radius: 20px;
    }
}